.header_right {
padding: 20px 20px;
height: 5%;
width: 80%;
box-shadow: 0 0  5px;
border-radius: 10px 10px;
display: block;
margin: 10px  10px 
}

.header {
overflow: hidden;
backdrop-filter: blur(2px);
background: url(/src/Assets/download.jpg);
padding: 10px 10px;
box-shadow: 0 0  10px;
border-radius: 5px;
width: 100%;
}

.header a {
float: left;
color:black;
text-align: center;
padding: 10px;
text-decoration: none;
font-size: 18px; 
line-height: 25px;
border-radius: 4px;
font-weight: 500;
}

.header a.logo {
font-size: 25px;
font-weight: bold;
}

.header a.logo:first-letter{
  font-size: 3rem;
  color: #1b5e20;
}

.header a:hover {
background-color:#c6c8ce;
color:#fff;
}


.header-right {
float: right;
}

@media screen and (max-width: 500px) {
.header a {
  float: none;
  display: block;
  text-align: left;
}

.header-right {
  float: none;
}
}


