.signup-button {
    outline: none;
    border: none;
    cursor: pointer;
    width: 95%;
    height: 40px;
    border-radius: 30px;
    font-size: 18px;
    font-weight: 700;
    color: #fff;
    text-align: center;
    background-color: #02c8db;
    box-shadow: 3px 3px 8px #b1b1b1, -3px -3px 8px #fff;
 }
 .signup-button:hover{
     background-color: #81d4fa;
 }
 .signup-form{
    border-radius: 20px;
    box-shadow: 0px 0px 10px ;
    padding: 10px;
    width: 300px;
    height: 500px;
    text-align: center;
    margin: 50px 500px;
    background-color: #f5f5f5;
 }
 .fields{
     width: 95%;
     padding: 5px 5px 5px 5px;
 }
 .fields input{
     border: none;
     outline: none;
     background: none;
     font-size: 18px;
     padding: 10px 10px 10px 5px;
     font-weight: 600;
 }
 .username,
 .password{
     margin-bottom: 20px;
     border-radius: 10px;
     box-shadow: inset 8px 8px 8px #cbced1, inset -8px -8px 8px #fff;
     padding: 5px;
 }
 .login-link{
     padding-top: 20px;
     text-align: center;
     font-size: 18px;
     font-weight: 700;
 }
 .logo-signup{
     background: url(/src/Assets/logo.jpg);
     width: 80px;
     height: 80px;
     border-radius: 50%;
     margin: 0 auto;
     padding-top: 5px;
     box-shadow: 0 0 2px #5f5f5f, 0 0 0 5px #ecf0f3, 8px 8px 15px #a7aaaf, -8px -8px 15px #fff;
 }
 