.flex{
    display: flex;
    gap: 20px;
    margin: 10px;
    justify-content: center;
    text-align: center;
}
.container{
	cursor: pointer;
	border-radius: 15px;
	height: 200px;
	width: 200px;
	position: relative;
	color: #000000;
	 box-shadow: 0 5px 10px 0 #0a0a0a7c;
     padding: 20px;
}
.container::after{
	content: "";
	background: #424242;
	border-radius: 15px;
	height: 100%;
	width: 100%;
	position: absolute;
	right: 0;
	bottom: 0;
	z-index: 0;
	clip-path: circle(10% at 100% 100%);
	transition: all 0.3s ease-in;
	
}
.content{
	position: relative;
	z-index: 1;
	transition: all 0.3s ease-in;
	font-family: 'Times New Roman', Times, serif;
}
.container:hover::after{
	clip-path: circle(100%)
}
.container:hover .content{
	color: #ffffff;
}
.content + .content {
    margin-left: -10%;
}

.home-page-banner{
    width: 100%;
    height: 500px;
    background: url(/src/Assets/download.jpg);
    padding: 10px;
   overflow: hidden;
}
.banner-text{
    color: #ffffff;
    padding: 20px ;
    margin:  10% 35%;
    width: 30%;
    font-size: 20px;
    font-weight: 600;
    font-family: Georgia, 'Times New Roman', Times, serif;
}
.glass-effect{
    border-radius: 10px;
    box-shadow: 20px 20px 50px rgba(0, 0, 0, 25);
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    border-right: 1px solid rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
}
.banner-heading{
    color: #ffffff;
}
.banner-heading:hover{
    color: #388e3c;
}
.testimonials{
    background-color: #ffffff;
}
.grid{
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    gap: 20px;
    margin: 20px 20px;
    justify-content: center;
    padding: 30px;
    text-align: center;
    font-size: 18px;
}
.grid-position{
    border-radius: 10px;
    padding: 10px 10px;
    box-shadow: 0 0  10px; 
    width: 250px;  
    height: 250px;
}
.box{
    width: 200px;
    height: 240px;
    margin: 10px;
    color: #ffffff;
}
.box > .front-box{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    transform: perspective(600px) rotateY(0deg);
    background: #388e3c;
    width: 200px;
    height: 240px;
    border-radius: 7px;
    backface-visibility: hidden;
    transition: transform 0.5s linear;
    
}
.box > .back-box{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    transform: perspective(600px) rotateY(180deg);
    background: #0d47a1;
    width: 200px;
    height: 240px;
    border-radius: 7px;
    backface-visibility: hidden;
    transition: transform 0.5s linear;
}
.box:hover > .front-box{
    transform: perspective(600px) rotateY(180deg);
}
.box:hover > .back-box{
    transform: perspective(600px) rotateY(0deg);
}
